import { useParams } from "react-router-dom";
import { useState } from 'react';
import DATA from "../data/Data";
import { useDispatch } from "react-redux";
import { addItem, delItem } from '../redux/actions/index';


const ProductDetails = () => {
  const [btntxt, setBtntxt] = useState('Add to Cart');
  const params = useParams();
  const paramsId = parseInt(params.id);
  const products = DATA.filter(e => e.id === paramsId);
  const dispatch = useDispatch();

  const product= products[0];

  const handleAddToCart = (product) => {
    if(btntxt === 'Add to Cart') {
      dispatch(addItem(product))
      setBtntxt('Remove from Cart')
    }
    else{
      dispatch(delItem(product))
      setBtntxt('Add to Cart')
    }
  }


  return <>
  <div className="container my-5 py-5">
      <div className="row">
        <div className="col-md-6 d-flex justify-content-center mx-auto product">
            <img src={product.img} alt={product.title}  height='400px' />
        </div>
        <div className="col-md-6 d-flex flex-column">
            <h1 className="display-6 fw-bold">{product.title}</h1>
            <hr />
            <h2 className="my-4">${product.price}</h2>
            <p className="lead">{product.desc}</p>
            <button className="btn btn-outline-primary" onClick={() => handleAddToCart(product)}>{btntxt}</button>
          </div>
        </div>
    </div>
  </>;
};

export default ProductDetails;
